import React from 'react'
import styled from 'styled-components'
import {useNavigate} from 'react-router-dom'
import { useState } from "react";
import {useSelector, useDispatch} from 'react-redux'
import axios from "axios";
const Sign = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [userId, setuserId] = useState('')
  const [password, setPassword] = useState('')
  const [passwordConfirm, setPasswordConfirm] = useState('')
  const [idMessage, setIdMessage] = useState('')
  const [passwordMessage, setPasswordMessage] = useState('')
  const [passwordConfirmMessage, setPasswordConfirmMessage] = useState('')


  const [isId, setIsId] = useState(false)
  const [isPassword, setIsPassword] = useState(false)
  const [isPasswordConfirm, setIsPasswordConfirm] = useState(false)

  const onChangeName = (e) => {
    const usernameRegex = /^(?=.*[a-zA-z0-9])(?=.*[a-zA-z]).{4,12}$/
    const usernameCurrnet = e.target.value 
    setuserId(usernameCurrnet)
    
    if(!usernameRegex.test(usernameCurrnet)){
        setIdMessage('  ')
        setIsId(false)
    }else{
        setIdMessage(' ')
        setIsId(true)
    }
};
const onChangePassword = (e) => {
  const passwordRegex = /^(?=.*[a-zA-z])(?=.*[0-9]).{8,32}$/
  const passwordCurrnet = e.target.value;
  setPassword(passwordCurrnet)

  if(!passwordRegex.test(passwordCurrnet)){
      setPasswordMessage('++  8  !')
      setIsPassword(false);
  }else{
      setPasswordMessage(' !')
      setIsPassword(true);
  }
}

const onChangePasswordConform = (e) => {
  const passwordConformRegex = /^(?=.+[a-zA-z])(?=.+[0-9]).{8,32}$/
  const passwordConformCurrnet = e.target.value;
  setPasswordConfirm(passwordConformCurrnet)

  if(!password===passwordConfirm){
      setPasswordConfirmMessage(' ')
      setIsPasswordConfirm(false);
  }else{
      setPasswordConfirmMessage(' ')
      setIsPasswordConfirm(true);
  }
}
const userInfo = {
  userId,
  password,
  passwordConfirm
}

const onSubmitHandler = async (e) => {
  e.preventDefault();
if( userId.trim()===""|| password.trim() === ""|| passwordConfirm.trim() === ""){
  return alert ('  ')
};   
if(password !== passwordConfirm){
  return alert('   .')};
if(isId !== true || isPassword !== true || isPasswordConfirm !== true ){
  return alert(' ')
}
const data = await axios.post(
  "http://3.39.254.156/api/member/signup",
  userInfo,{
      headers:{
          "Content-Type": "application/json",
      }
  }
);
console.log(data)
if(data.data.success){
  navigate('/login');
}else{
  window.alert(data.error.message)
}


}  
  return (
    <div>
      <StTit><h1></h1><p>   .</p></StTit>
      
      <StForm>
      
      <StTbody >
        <tr>
          <StSignUpTit></StSignUpTit>
          <StSignTd><StInputWrap  >
            <span>
              <StInput maxlength="20" type="text" name='username' placeholder="6~20,  ,  ."  value={userId} onChange={onChangeName}/>
              <StButton > </StButton>
            </span>
          </StInputWrap></StSignTd>
        </tr>
        <tr>
          <StSignUpTit></StSignUpTit>
          <StSignTd>
            <StInputWrap >
              <span>
                <StInput   maxlength="20"  title="  " type="password" placeholder="8~20, ,,  2  ." value={password} onChange={onChangePassword} />
              </span>
            </StInputWrap>
          </StSignTd>
        </tr>
        <tr>
          <StSignUpTit> </StSignUpTit>
          <StSignTd>
            <StInputWrap >
              <span>
                <StInput value={passwordConfirm} onChange={onChangePasswordConform}  maxlength="20"  title="   " type="password" placeholder="8~20, ,,  2  ." />
              </span>
            </StInputWrap>
          </StSignTd>
        </tr>
      </StTbody>
      <StSignButtonWrap>
        <StSignButton onClick={onSubmitHandler}  > </StSignButton>
      </StSignButtonWrap>
      </StForm>
    </div>
  )
}

