import React from 'react'
import styled from 'styled-components'
import { useEffect, useState } from "react";
import {useSelector, useDispatch} from 'react-redux'
import {useNavigate} from 'react-router-dom'
import {__userLogin} from '../../redux/modules/users'
const LoginPage = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const initialstate = {
        userId:"",
        password:"",
    }
    const [user,setUser] = useState(initialstate);
        // console(user)
    const onChangeHandler = (e) => {
        const {name, value} = e.target;
        setUser({...user, [name]: value})
    };
    return (
        <StContainer>
            <StTitbox><StTit></StTit></StTitbox>
            <StLoginContainer>

                <StLoginTit>
                    
                </StLoginTit>
                <StLoginBox>
                    <StLoginForm>
                        <form action="">
                        <StInput maxLength={20} placeholder=" " type="text" name='userId' value={user.userId} onChange={onChangeHandler}/>
                        <StInput placeholder=' ' type="password" name='password' value={user.password} onChange={onChangeHandler}/>
                        <StButton type='button' onClick={() => {
                dispatch(__userLogin(user));}}></StButton>
                        <div>
                            <p type="button" onClick={()=>{navigate("/signup")}}>  ?</p>
                        </div>
                        </form>
                    </StLoginForm>
                    <div>
                        <h1>SNS   </h1>
                        <StSnsLogin>
                            <StLi><img src="https://contents-image.twayair.com/homepage/images/member/sns_c_kakao.png" alt="" />
                                <p> </p></StLi>
                            <StLi><img src="https://contents-image.twayair.com/homepage/images/member/sns_c_naver.png" alt="" />
                                <p> </p></StLi>
                            <StLi><img src="https://contents-image.twayair.com/homepage/images/member/sns_c_fb.png" alt="" />
                                <p> </p></StLi>
                        </StSnsLogin>
                    </div>
                </StLoginBox>
            </StLoginContainer>
        </StContainer>
    )
}

export default LoginPage
const StContainer = styled.div`
    width: 100%;
`
const StTitbox = styled.div`
width: 100%;
    margin: 0 auto;
`
const StTit = styled.h1`
width: 1200px;
text-align: center;
    margin:  0 auto;
    padding: 30px;
    
`
const StLoginTit = styled.h2`
    width: 1200px;
    margin: 0 auto 100px auto;
    text-align: center;
    padding: 20px;
    border-left:  1px solid #eee;
    border-right:  1px solid #eee;
    border-top: 4px solid #d22c26;
`
const StLoginBox = styled.div`
    display: flex;
    justify-content: center;
    gap: 100px;
`
const StLoginContainer = styled.div`
    width: 1200px;
    margin: 0 auto;
`
const StInput = styled.input`
    width: 480px;
    height: 46px;
    border: 1px solid #eee;
    display: block;
    padding:  0px  0px 0px 20px;
    font-size: 16px;
    margin-bottom: 10px;
`
const StButton = styled.button`
    width: 100%;
    height: 60px;
    background-color: #d22c26;
    border: none;
    border-radius: 5px;
    color: #fff;
    font-size: 20px;
    font-weight: 600;
`
const StLoginForm = styled.div`
    width: 500px;
    padding-right: 40px;
    border-right: 1px solid #eee;
`
const StSnsLogin = styled.ul`
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
`
const StLi = styled.li`
    text-align: center;
`