import React from 'react'

import { useState ,useEffect} from 'react';
import './style.css'
import { getCookie } from '../../shared/Cookie';
import { useNavigate } from 'react-router-dom';

import { useDispatch } from 'react-redux';
import MypageModal from './MypageModal';
const Header = () => {
    const [modal, setModal] = useState(false);

    const onModalHandler = () => {
      setModal(!modal);
    };
  
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const token = getCookie("isLogin")
    console.log(token)

    const [ScrollY, setScrollY] = useState(0); // window  pageYOffset  
    const [ScrollActive, setScrollActive] = useState(false);
    function handleScroll() {
        if (ScrollY > 90) {
            setScrollY(window.pageYOffset);
            setScrollActive(true);
        } else {
            setScrollY(window.pageYOffset);
            setScrollActive(false);
        }
    }
    useEffect(() => {
        function scrollListener() { window.addEventListener("scroll", handleScroll); } //  window    
        scrollListener(); // window   
        return () => { window.removeEventListener("scroll", handleScroll); }; //  window    
    });
    return (
        <>
            <div className={ScrollActive ? "fixedBox Stcontainer" : "Stcontainer"}>
                <header className='const StHeaderContainer'>
                    <h1 onClick={()=>{navigate("/")}} className={ScrollActive ? "fixedLogo" : 'StLogo'}></h1>
                    <nav className='StNavBox'>
                        <ul className='StNavContainer'>

                            {token == null ? <li onClick={() => {alert("  ");navigate("/login")}} className={ScrollActive ? "fixedBox1" : "StNavList"}></li> : <li  onClick={() => {navigate("/search")}} className={ScrollActive ? "fixedBox1" : "StNavList"}></li>}
                            {token == null ? <li onClick={() => {alert("  ");navigate("/login")}} className={ScrollActive ? "fixedBox1" : "StNavList"}> </li> : <li  onClick={() => {navigate("/mypage")}} className={ScrollActive ? "fixedBox1" : "StNavList"}> </li>}

                            <li className={ScrollActive ? "fixedBox1" : "StNavList"}></li>
                            <li className={ScrollActive ? "fixedBox1" : "StNavList"}></li>
                            <li className={ScrollActive ? "fixedBox1" : "StNavList"}></li>
                            <li className={ScrollActive ? "fixedBox1" : "StNavList"}></li>
                        </ul>
                    </nav>
                    <div className='StSideBox'>
                        {token == null ? <p onClick={() => {alert("  "); navigate("/login") }} className={ScrollActive ? "fixedMyPage" : "StMyPage"}></p> : <p onClick={onModalHandler} className={ScrollActive ? "fixedMyPage2" : "StMyPage2"}></p>}
                        
                       
            {modal == true ?<MypageModal onChange = {onModalHandler}/>:null}
            
                    </div>
                </header>
            </div>
            
        </>
    )
}

export default Header




